import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'layout',
      redirect: '/business',
      component: () => import('@/views/main/layout'),
      children: [{
        path: '/business',
        name: 'business',
        component: () => import('@/views/main/business')
      }, {
        path: '/finance',
        name: 'finance',
        component: () => import('@/views/main/finance')
      }, {
        path: '/personInfo',
        name: 'personInfo',
        component: () => import('@/views/main/personInfo')
      }]
    }
  ]
})



